@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400&display=swap');

* {
    box-sizing: border-box;
    padding: auto;
}

body {
    user-select: none;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    overflow: hidden;
    margin: 0;
}

::-webkit-scrollbar {
    width: 1vw;
}

::-webkit-scrollbar-track {
    background: #bbbbbb;
}

::-webkit-scrollbar-thumb {
    background: #555555;
}

#teleprompter {
    z-index: 0;
    width: 100vw;
    height: 100vh;
}

#controller {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    gap: 3vw;
    justify-content: center;
    background-color: #222222;
    color: #ffffff;
    position: fixed;
    z-index: 2;
    height: 10vh;
    width: 100vw;
}

#logo {
    display: grid;
    justify-items: center;
    align-items: center;
}

#main-buttons-group {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 0.5vw;
    align-content: center;
    justify-content: center;
}

.main-buttons {
    width: 4.5em;
}

#start-stop, #default {
    font-weight: bold;
}

#mode-group {
    display: grid;
    grid-template-columns: auto auto;
    gap: 0.5vw;
    align-content: center;
    text-align: right;
}

.mode-buttons {
    width: 4.5em;
}

#settings {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 0.5vh 1vw;
    align-content: center;
    justify-content: center;
    text-align: right;
}

#default-container {
    align-content:space-around;
    margin: auto;
}

#default {
    width: 5em;
}

#text-container, #text-display {
    position: absolute;
    z-index: 1;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    border: 0px;
    margin: 0;
}

#text-container {
    outline: none;
    resize: none;
    overflow-y: scroll;
}

#text-display {
    height: auto;
    overflow: hidden;
    white-space: pre-wrap; 
    word-break: break-word;
    padding-right: 0.25vw;
}

#text-marker {
    position: absolute;
    margin: 0;
}

.transparent {
    opacity: 0.5;
    transition-property: opacity;
    transition-duration: 0.6s;
}

.visible {
    opacity: 1;
    transition-property: opacity;
    transition-duration: 0.6s;
}

.dark {
    background-color: #000000;
    color: #ffffff;
}

.light {
    background-color: #ffffff;
    color: #000000;
}
